<template>
  <div class="component py-4">
    <h5 class="component__heading pt-0 pt-lg-1 px-3 px-lg-5">Deposit in to the account</h5>
    <div class="row">
      <div class="col-lg-9 col-xl-7">
        <BetForm class="px-3 px-lg-5">
          <form @submit.prevent="depositFunds()">
            <div class="form-group">
              <div class="input-group">
                <div class="input-group-prepend">
                  <div class="input-group-text" id="enterAmount">Ksh</div>
                </div>
                <input type="number" class="form-control rounded-0"
                    v-model="deposit.amount"
                    required
                    id="amount" aria-describedby="phoneHelp" placeholder="Enter the Amount">
              </div>

            </div>
            <div class="form-group">
              <div id="amountHelp" class="form-text text-danger">
                {{ depositResponseErrors ? depositResponseErrors.message : '' }}
              </div>
              <div class="bg-success text-center p-2" v-show="depositResponseData">
                {{ depositResponseData ? depositResponseData.ResponseDescription : '' }}
              </div>
            </div>
            <div class="form-group d-flex">
              <BetButton type="submit" :disabled="!deposit.amount || formSubmitted">Deposit</BetButton>              
            </div>
          </form>
        </BetForm>
      </div>
    </div>
  </div>
</template>

<script>
  import {mapGetters} from 'vuex'
  import {CUSTOMER_DEPOSIT_URL} from '../../api_routes'
  import axios from 'axios'
  import {AUTH_CONFIG} from '../../auth_config'
  import BetForm from '../Utilities/BetForm'
  import BetButton from '../Utilities/BetButton'

  export default {
    name: 'Deposit',
    components: {BetForm, BetButton},
    data () {
      return {
        deposit: {
          amount: ''
        },
        depositResponseData: null,
        formSubmitted: false,
        depositResponseErrors: null
      }
    },
    computed: {...mapGetters(['authUser'])},
    methods: {
      depositFunds () {
        this.formSubmitted = true
        let postData = {
          msisdn: this.authUser.phone,
          amount: this.deposit.amount
        }
        axios.post(CUSTOMER_DEPOSIT_URL, postData, {headers: AUTH_CONFIG.getHeaders()}).then((response) => {
          if (response.status === 200) {
            this.depositResponseData = response.data
            this.$store.dispatch('init_auth_user', {request: 'refresh'})
            this.deposit.amount = ''
            this.formSubmitted = true
          }
        }, (error) => {
          this.formSubmitted = true
          let responseErrors = error.response.data
          this.depositResponseErrors = responseErrors
          this.$noty.error('Sorry ' + this.depositResponseErrors.message + 'Please try again after 4 minutes')
          console.log('Deposit errors', responseErrors)
        })
      },
      refresh () {
        this.$store.dispatch('init_auth_user', {request: 'refresh'})
        this.$router.push({name: 'profile'})
      }
    }
  }
</script>

<style scoped lang="stylus">
  .component
    background-image url(/img/bg-deposit.png)
    background-repeat no-repeat
    background-position right 36px bottom 20px
    background-size 25% auto
    min-height 400px

    @media (max-width 1024px)
      background-position right 25px bottom 25px
      background-size 20% auto
      min-height 240px

    @media (max-width 768px)
      background-image none
      min-height 0
</style>
